#navbar {
  list-style-type: none;
  display: flex;
  width: 100%;
  background-color: darkgray;
  background-color: #5a2328;
  padding-left: 0;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#navbar .link {
  text-decoration: none;
  color: #f7f9f7;
  padding: 25px 45px;
  font-size: 20px;
  font-family: "Itim", cursive;
}
#navbar li {
  /* width : 20%; */
  padding: 20px 0;
}
#navbar li:hover {
  background-color: #d3bdb0;
}
#navbar li:hover .link {
  color: #f7f9f7;
}

#content {
  height: 80vh;
  color: goldenrod;
  padding: 10px 20px;
  color: #715b64;
  font-family: "Trispace", sans-serif;
}

#shop-container {
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
}
#shop-container img {
  width: 150px;
  height: 150px;
  margin-top: 20px;
}
.card-style {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card-title-style {
  font-size: medium;
  font-weight: 600;
  font-family: "Itim", cursive;
}
#order-title {
  margin-bottom: 40px;
}
#message {
  font-size: 25px;
  font-family: "Itim", cursive;
}
.heading-background-color {
  background-color: #d3bdb0;
  font-family: "Itim", cursive;
}
.dashboard-link {
  color: #5a2328;
  text-decoration: none;
  font-size: 14px;
  padding: 10px 20px;
  border: 2px solid #5a2328;
  background-color: #d3bdb0;
  color: white;
  margin: 30px;
}

.dashboard-link:hover {
  color: #5a2328;
}
#dashboard-heading {
  margin-bottom: 40px;
}
#signin-form {
  margin: 80px;
  display: flex;
  flex-direction: column;
  width: 400px;
}
#signin-form > input {
  width: 250px;
  border: none;
  outline: none;
  font-size: 18px;
  border-bottom: 1px solid #5a2328;
  font-family: "Times New Roman", Times, serif;
  margin-bottom: 30px;
}
#login-button {
  background-color: #5a2328;
  color: white;
  outline: none;
  border: none;
  padding: 10px 20px;
}
